@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.search {
    container: search / inline-size;

    @include tablet {
      container-type: normal;
    }

    &__filters,
    &__instruction {
      margin-bottom: 15px;
    }

    &__result {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
    }

    &__loading-spinner {
      margin-top: 15px;
    }

    &__loading-spinner {
      opacity: 0;

      &_visible {
        opacity: 1;
      }
    }

    &__load-more {
      text-align: center;
      margin-top: 20px;

      &_invisible {
        opacity: 0;
      }
    }
}
